import React, { Component } from "react";
import { Link } from 'react-router';

export default class Home extends Component {
  render() {
    return 
    <div>
      <ul>
        <Link to="/showlifecycle"><li>生命周期展示</li></Link>
      </ul>
    </div>;
  }
}



